import request, { Method } from "@/utils/request.js";

// ------------------编写百科-----------------------
/**
 * 获取百科类型选项
 */
export function getWikiTypeOptions() {
    return request({
      url: '/wiki/type-options',
      method: Method.GET,
      needToken: false,
      headers: {  'Content-Type': 'application/json'}
    });
}
/**
 * 获取百科模板选项
 */
export function getWikiTemplateOptions() {
    return request({
      url: '/wiki/template-options',
      method: Method.GET,
      needToken: false,
      headers: {  'Content-Type': 'application/json'}
    });
}

/**
 * 获取百科模板信息
 */
export function getWikiTemplate(name) {
    return request({
      url: `/wiki/template/${name}`,
      method: Method.GET,
      needToken: false,
      headers: {  'Content-Type': 'application/json'}
    });
}
/**
 * 发布百科
 */
export function publishWiki(params) {
    return request({
      url: '/wiki/my/publish',
      method: Method.POST,
      needToken: true,
      data: params,
      headers: {  'Content-Type': 'application/json'},
    });
}
/**
 * 修改百科
 */
export function editWiki(params) {
  return request({
    url: '/wiki/my/edit',
    method: Method.PUT,
    needToken: true,
    data: params,
    headers: {  'Content-Type': 'application/json'},
  });
}
/**
 * 删除百科
 */
export function deleteWiki(params) {
  return request({
    url: '/wiki/my/delete',
    method: Method.DELETE,
    needToken: true,
    data: params,
    headers: {  'Content-Type': 'application/json'},
  });
}
