// ------------------百科详细-----------------------

/**
 * 获取用户百科收藏信息
 */
export function getWikiCollectionByWikiId(params) {
  return request({
    url: '/wiki/my/collection',
    method: Method.POST,
    needToken: true,
    params,
    headers: {  'Content-Type': 'application/json'}
  });
}

/**
 * 获取用户所有百科收藏信息
 */
