<template>
  <div>
    <el-row style="padding: 0 20px">
        <el-select @change="getWikiCardsPage" style="width: 120px" v-model="wikiState" placeholder="稿件状态">
            <el-option
            v-for="item, index in wikiStateOptions"
            :key="index"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
        <el-select @change="getWikiCardsPage" style="margin-left: 14px; width: 120px;" v-model="wikiType" placeholder="百科类型">
            <el-option
            v-for="item, index in wikiTypeOptions"
            :key="index"
            :label="item.label"
            :value="item.name">
            </el-option>
        </el-select>
        <el-select style="margin-left: 14px; width: 120px" v-model="wikiOrder" placeholder="排序方式">
            <el-option
            v-for="item, index in wikiOrderOptions"
            :key="index"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
        <el-switch style="margin-left: 14px;"
        v-model="wikiOrderType"
        @change="getWikiCardsPage"
        active-text="升序"
        active-value="asc"
        inactive-text="降序"
        inactive-value="desc">
        </el-switch>
        <el-input class="my-wiki-search"
            placeholder="搜索百科"
            clearable
            v-model="wikiSearchTag">
            <i @click="myWikiSearch" slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input>
    </el-row>
    <el-main>
        

        <!-- <el-skeleton class="card-container" :loading="loading" animated :count="3">
            <template slot="template">
                <div class="card-body">
                    <el-skeleton-item variant="image" class="card-image" />
                    <div style="padding: 14px;" class="card-info">
                        <div>
                            <el-skeleton-item variant="h6" class="card-info-title" />
                        </div>
                        <p>
                            <el-skeleton-item variant="text" />
                        </p>
                        <el-skeleton-item variant="text" style="margin-top: 0px;"/>
                    </div>
                </div>
            </template>
        </el-skeleton> -->


        <div class="card-container">

            
            <el-card  class="card-body" v-for="(item) in cardsData" :key="item.wikiId" 
            :body-style="{padding: '0px' }">
                <div class="card-image" @click="toAnimlDetail(item.id)">
                    <a><img :src=item.cover class="image"></a>
                </div>
                <div style="padding: 14px;" class="card-info">
                    <div>
                        <h6 class="card-info-title">{{ item.title }}</h6>
                    </div>
                    <p>
                        {{ item.description }}
                    </p>
                    <div style="margin-top: 0px;color: #828282;">{{ item.createTime }}</div>
                    <el-row style="line-height: 24px;margin-top: 10px; color: #656565;">
                        <el-col :span="6">
                            <el-tag v-if="item.wikiState == 'APPROVED'" size="small" type="success">正常</el-tag>
                            <el-tag v-if="item.wikiState == 'IN_REVIEW'" size="small" type="warning">审核中</el-tag>
                            <el-tag v-if="item.wikiState == 'LOCKING'" size="small" type="danger">锁定中</el-tag>
                        </el-col>
                        <div class="card-info-icons">
                            <el-col :span="4">
                                <i class="el-icon-view"></i>
                                <span >{{ 0 }}</span>
                            </el-col>
                            <el-col :span="4">
                                <i class="el-icon-star-on"></i>
                                <span >{{ 0 }}</span>
                            </el-col>
                            <el-col :span="4">
                                <i class="el-icon-chat-dot-round"></i>
                                <span >{{ 0 }}</span>
                            </el-col>
                        </div>
                    
                    </el-row>
                    
                        
                    <div class="card-option-wrap">
                        <div class="card-option-btn-wrap">
                            <el-button @click="toAnimlDetail(item.id)" class="card-option-btn" type="info" icon="el-icon-s-marketing" plain circle></el-button>
                            <el-button @click="editWiki(item)" class="card-option-btn" type="primary" icon="el-icon-edit" plain circle></el-button>
                            <el-button @click="delWiki(item)" class="card-option-btn" type="danger" icon="el-icon-delete" plain circle></el-button>
                        </div>
                        
                    </div>
                    
                </div>
            </el-card>
        </div>
        <div class="block">
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            :page-sizes="[12, 16, 20, 24]"
            layout="sizes, prev, pager, next, total, jumper"
            :total="total">
            </el-pagination>
        </div>
    </el-main>

  </div>
</template>

<script>
import * as apiWiki from '@/api/wiki';
import storage from '@/plugins/storage';
import { data } from 'jquery';
export default {
  components: {},
  props: {},
  data() {
    return  {
        wikiSearchTag: '',
        wikiType: null,
        wikiTypeOptions: [],
        wikiState: '',
        wikiStateOptions: [{
            value: '',
            label: '所有'
        }, {
            value: 'APPROVED',
            label: '正常'
        }, {
            value: 'IN_REVIEW',
            label: '审核中'
        }, {
            value: 'LOCKING',
            label: '锁定中'
        }],
        wikiOrder: 'create_time',
        wikiOrderType: 'desc',
        wikiOrderOptions: [{
            value: 'create_time',
            label: '发布时间'
        }, {
            value: 'title',
            label: '百科标题'
        }, {
            value: 'view_count',
            label: '访问数量'
        }, {
            value: 'comment_count',
            label: '评论数量'
        }, {
            value: 'collection_count',
            label: '收藏数量'
        }],
        currentPage: 1,
        pageSize: 12,
        total: 10,
        cardsData: [{"id":"1711581956116336640","title":"拉布拉多犬","cover":"http://s1osmcysm.hn-bkt.clouddn.com/wiki-cover/2/0/eec21b3f-f589-406f-aea0-feeeb838f4fd.jpg","description":" The Ewokian is a designer dog breed developed by crossing two toy dogs, the Havanese and the Pomeranian. Their teddy-bear look charms everybody!  ","about":"[{\"name\":\"Height\",\"value\":\"12-14 inches\"},{\"name\":\"Weight\",\"value\":\"7-17 lb\"},{\"name\":\"Physique\",\"value\":\"Sturdy, big bodied cats\"},{\"name\":\"Lifespan\",\"value\":\"8-10 years\"},{\"name\":\"Best Suited For\",\"value\":\"Families and singles with or without children and pets\"},{\"name\":\"Temperament\",\"value\":\"Calm, easy going, adaptable, affectionate\"},{\"name\":\"Comparable Breeds\",\"value\":\"British Longhair, Ragamuffin\"}]","content":"<h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedHistory</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The oldest of all British cat breeds, the Shorthair traces its lineage back to Roman domestic cats. The ancestors of the British Shorthair were initially prized for their skill as hunters. Big and strong, they were more than a match for any rat or mouse, and thus earned their keep living in proximity to humans. Once a street cat, the British Shorthair is now highly prized as a companion and beloved for its beauty and engaging, loyal personality. The British Shorthair became an officially recognized breed in 1889, principally due to the work of enthusiast Harrison Weir. The first British Shorthair to earn a Grand Champion title under the rules of the Governing Council of the Cat Fancy was a blue male named Brynbuboo Little Monarch. Today, virtually all British Shorthairs trace their lineage back to this one cat.The British Shorthair is an adaptable and calm breed.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/8/a992dcd6-185a-4a29-ae69-0561ed00abd2.jpg\" alt=\"\" title=\"\" width=\"459\" height=\"263\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedTraits</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair is an adaptable and calm breed. They learn quickly, and get along well with all members of the household, although they do tend to bond more deeply with the human of their choice. They don’t like to be carried, but other than that the British Shorthair is an easy going cat that doesn’t have much to say. Somewhat rowdy as kittens, they grow out of their boisterous phase to become excellent companions and members of the family. The British Shorthair is not especially vocal, nor are they bad to wander away. They can be left alone without suffering from either separation anxiety or loneliness.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/11/12/b8f16391-88fb-4bdf-86fd-9b93e34dba30.jpg\" alt=\"\" title=\"\" width=\"446\" height=\"243\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">OverallDescription</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">Often described as having a “teddy bear” appearance, the British Shorthair is a sturdy cat with a large head and big, round, inquisitive eyes. The breed is slow to mature, not reaching full size until at least three years of age. They are big cats, with males reaching as much as 17 pounds. Powerful and well balanced, the Shorthair is broad of chest and short of leg with rounded paws and a tail that narrows to a rounded tip from a thick base. Small ears top a sizeable head made to appear even wider thanks to the cat’s full cheeks.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/12/4b9b708f-e321-4e38-ae71-e8d66cd33dc2.jpg\" alt=\"\" title=\"\" width=\"447\" height=\"254\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">Colors</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair comes in many patterns and colors. The blue variant has been widely popular, but in recent years the black, white, red, cream, chocolate, lilac, and cinnamon variations have grown in prevalence. The cats can be all one color, or they can be colorpoints, tabbies, and bicolors.</span></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">GroomingRequirements</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The coat of the British Shorthair can be compared to a carpet with a deep pile. They have more hairs per square inch in their coats than any other breed. Surprisingly, this luxurious coat requires very little care beyond brushing or combing with a wire toothed comb to remove dead and loose hairs. This should be done on a weekly basis to minimize shedding.Photo credit: Eric Isselee/ShutterstockPhoto credit: Eric Isselee/Shutterstock</span></p>","contentType":"HTML","createTime":"2023-10-10T11:18:33","updateTime":"2023-10-10T21:18:40","wikiType":"dog","wikiState":"IN_REVIEW","creatorId":"1001","collectionCount":0},{"id":"1711581956871311360","title":"拉布拉多犬","cover":"http://s1osmcysm.hn-bkt.clouddn.com/wiki-cover/6/11/3b76baf0-460f-40f5-afa5-d6117aa9551b.jpg","description":" The Ewokian is a designer dog breed developed by crossing two toy dogs, the Havanese and the Pomeranian. Their teddy-bear look charms everybody!  ","about":"[{\"name\":\"Height\",\"value\":\"12-14 inches\"},{\"name\":\"Weight\",\"value\":\"7-17 lb\"},{\"name\":\"Physique\",\"value\":\"Sturdy, big bodied cats\"},{\"name\":\"Lifespan\",\"value\":\"8-10 years\"},{\"name\":\"Best Suited For\",\"value\":\"Families and singles with or without children and pets\"},{\"name\":\"Temperament\",\"value\":\"Calm, easy going, adaptable, affectionate\"},{\"name\":\"Comparable Breeds\",\"value\":\"British Longhair, Ragamuffin\"}]","content":"<h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedHistory</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The oldest of all British cat breeds, the Shorthair traces its lineage back to Roman domestic cats. The ancestors of the British Shorthair were initially prized for their skill as hunters. Big and strong, they were more than a match for any rat or mouse, and thus earned their keep living in proximity to humans. Once a street cat, the British Shorthair is now highly prized as a companion and beloved for its beauty and engaging, loyal personality. The British Shorthair became an officially recognized breed in 1889, principally due to the work of enthusiast Harrison Weir. The first British Shorthair to earn a Grand Champion title under the rules of the Governing Council of the Cat Fancy was a blue male named Brynbuboo Little Monarch. Today, virtually all British Shorthairs trace their lineage back to this one cat.The British Shorthair is an adaptable and calm breed.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/8/a992dcd6-185a-4a29-ae69-0561ed00abd2.jpg\" alt=\"\" title=\"\" width=\"459\" height=\"263\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedTraits</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair is an adaptable and calm breed. They learn quickly, and get along well with all members of the household, although they do tend to bond more deeply with the human of their choice. They don’t like to be carried, but other than that the British Shorthair is an easy going cat that doesn’t have much to say. Somewhat rowdy as kittens, they grow out of their boisterous phase to become excellent companions and members of the family. The British Shorthair is not especially vocal, nor are they bad to wander away. They can be left alone without suffering from either separation anxiety or loneliness.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/11/12/b8f16391-88fb-4bdf-86fd-9b93e34dba30.jpg\" alt=\"\" title=\"\" width=\"446\" height=\"243\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">OverallDescription</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">Often described as having a “teddy bear” appearance, the British Shorthair is a sturdy cat with a large head and big, round, inquisitive eyes. The breed is slow to mature, not reaching full size until at least three years of age. They are big cats, with males reaching as much as 17 pounds. Powerful and well balanced, the Shorthair is broad of chest and short of leg with rounded paws and a tail that narrows to a rounded tip from a thick base. Small ears top a sizeable head made to appear even wider thanks to the cat’s full cheeks.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/12/4b9b708f-e321-4e38-ae71-e8d66cd33dc2.jpg\" alt=\"\" title=\"\" width=\"447\" height=\"254\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">Colors</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair comes in many patterns and colors. The blue variant has been widely popular, but in recent years the black, white, red, cream, chocolate, lilac, and cinnamon variations have grown in prevalence. The cats can be all one color, or they can be colorpoints, tabbies, and bicolors.</span></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">GroomingRequirements</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The coat of the British Shorthair can be compared to a carpet with a deep pile. They have more hairs per square inch in their coats than any other breed. Surprisingly, this luxurious coat requires very little care beyond brushing or combing with a wire toothed comb to remove dead and loose hairs. This should be done on a weekly basis to minimize shedding.Photo credit: Eric Isselee/ShutterstockPhoto credit: Eric Isselee/Shutterstock</span></p>","contentType":"HTML","createTime":"2023-10-10T11:18:33","updateTime":null,"wikiType":"dog","wikiState":"APPROVED","creatorId":"1001","collectionCount":0},{"id":"1711581957630480384","title":"拉布拉多犬","cover":"http://s1osmcysm.hn-bkt.clouddn.com/wiki-cover/6/11/3b76baf0-460f-40f5-afa5-d6117aa9551b.jpg","description":" The Ewokian is a designer dog breed developed by crossing two toy dogs, the Havanese and the Pomeranian. Their teddy-bear look charms everybody!  ","about":"[{\"name\":\"Height\",\"value\":\"12-14 inches\"},{\"name\":\"Weight\",\"value\":\"7-17 lb\"},{\"name\":\"Physique\",\"value\":\"Sturdy, big bodied cats\"},{\"name\":\"Lifespan\",\"value\":\"8-10 years\"},{\"name\":\"Best Suited For\",\"value\":\"Families and singles with or without children and pets\"},{\"name\":\"Temperament\",\"value\":\"Calm, easy going, adaptable, affectionate\"},{\"name\":\"Comparable Breeds\",\"value\":\"British Longhair, Ragamuffin\"}]","content":"<h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedHistory</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The oldest of all British cat breeds, the Shorthair traces its lineage back to Roman domestic cats. The ancestors of the British Shorthair were initially prized for their skill as hunters. Big and strong, they were more than a match for any rat or mouse, and thus earned their keep living in proximity to humans. Once a street cat, the British Shorthair is now highly prized as a companion and beloved for its beauty and engaging, loyal personality. The British Shorthair became an officially recognized breed in 1889, principally due to the work of enthusiast Harrison Weir. The first British Shorthair to earn a Grand Champion title under the rules of the Governing Council of the Cat Fancy was a blue male named Brynbuboo Little Monarch. Today, virtually all British Shorthairs trace their lineage back to this one cat.The British Shorthair is an adaptable and calm breed.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/8/a992dcd6-185a-4a29-ae69-0561ed00abd2.jpg\" alt=\"\" title=\"\" width=\"459\" height=\"263\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedTraits</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair is an adaptable and calm breed. They learn quickly, and get along well with all members of the household, although they do tend to bond more deeply with the human of their choice. They don’t like to be carried, but other than that the British Shorthair is an easy going cat that doesn’t have much to say. Somewhat rowdy as kittens, they grow out of their boisterous phase to become excellent companions and members of the family. The British Shorthair is not especially vocal, nor are they bad to wander away. They can be left alone without suffering from either separation anxiety or loneliness.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/11/12/b8f16391-88fb-4bdf-86fd-9b93e34dba30.jpg\" alt=\"\" title=\"\" width=\"446\" height=\"243\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">OverallDescription</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">Often described as having a “teddy bear” appearance, the British Shorthair is a sturdy cat with a large head and big, round, inquisitive eyes. The breed is slow to mature, not reaching full size until at least three years of age. They are big cats, with males reaching as much as 17 pounds. Powerful and well balanced, the Shorthair is broad of chest and short of leg with rounded paws and a tail that narrows to a rounded tip from a thick base. Small ears top a sizeable head made to appear even wider thanks to the cat’s full cheeks.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/12/4b9b708f-e321-4e38-ae71-e8d66cd33dc2.jpg\" alt=\"\" title=\"\" width=\"447\" height=\"254\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">Colors</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair comes in many patterns and colors. The blue variant has been widely popular, but in recent years the black, white, red, cream, chocolate, lilac, and cinnamon variations have grown in prevalence. The cats can be all one color, or they can be colorpoints, tabbies, and bicolors.</span></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">GroomingRequirements</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The coat of the British Shorthair can be compared to a carpet with a deep pile. They have more hairs per square inch in their coats than any other breed. Surprisingly, this luxurious coat requires very little care beyond brushing or combing with a wire toothed comb to remove dead and loose hairs. This should be done on a weekly basis to minimize shedding.Photo credit: Eric Isselee/ShutterstockPhoto credit: Eric Isselee/Shutterstock</span></p>","contentType":"HTML","createTime":"2023-10-10T11:18:33","updateTime":null,"wikiType":"dog","wikiState":"IN_REVIEW","creatorId":"1001","collectionCount":0},{"id":"1711581954753187840","title":"拉布拉多犬","cover":"http://s1osmcysm.hn-bkt.clouddn.com/wiki-cover/6/11/3b76baf0-460f-40f5-afa5-d6117aa9551b.jpg","description":" The Ewokian is a designer dog breed developed by crossing two toy dogs, the Havanese and the Pomeranian. Their teddy-bear look charms everybody!  ","about":"[{\"name\":\"Height\",\"value\":\"12-14 inches\"},{\"name\":\"Weight\",\"value\":\"7-17 lb\"},{\"name\":\"Physique\",\"value\":\"Sturdy, big bodied cats\"},{\"name\":\"Lifespan\",\"value\":\"8-10 years\"},{\"name\":\"Best Suited For\",\"value\":\"Families and singles with or without children and pets\"},{\"name\":\"Temperament\",\"value\":\"Calm, easy going, adaptable, affectionate\"},{\"name\":\"Comparable Breeds\",\"value\":\"British Longhair, Ragamuffin\"}]","content":"<h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedHistory</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The oldest of all British cat breeds, the Shorthair traces its lineage back to Roman domestic cats. The ancestors of the British Shorthair were initially prized for their skill as hunters. Big and strong, they were more than a match for any rat or mouse, and thus earned their keep living in proximity to humans. Once a street cat, the British Shorthair is now highly prized as a companion and beloved for its beauty and engaging, loyal personality. The British Shorthair became an officially recognized breed in 1889, principally due to the work of enthusiast Harrison Weir. The first British Shorthair to earn a Grand Champion title under the rules of the Governing Council of the Cat Fancy was a blue male named Brynbuboo Little Monarch. Today, virtually all British Shorthairs trace their lineage back to this one cat.The British Shorthair is an adaptable and calm breed.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/8/a992dcd6-185a-4a29-ae69-0561ed00abd2.jpg\" alt=\"\" title=\"\" width=\"459\" height=\"263\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedTraits</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair is an adaptable and calm breed. They learn quickly, and get along well with all members of the household, although they do tend to bond more deeply with the human of their choice. They don’t like to be carried, but other than that the British Shorthair is an easy going cat that doesn’t have much to say. Somewhat rowdy as kittens, they grow out of their boisterous phase to become excellent companions and members of the family. The British Shorthair is not especially vocal, nor are they bad to wander away. They can be left alone without suffering from either separation anxiety or loneliness.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/11/12/b8f16391-88fb-4bdf-86fd-9b93e34dba30.jpg\" alt=\"\" title=\"\" width=\"446\" height=\"243\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">OverallDescription</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">Often described as having a “teddy bear” appearance, the British Shorthair is a sturdy cat with a large head and big, round, inquisitive eyes. The breed is slow to mature, not reaching full size until at least three years of age. They are big cats, with males reaching as much as 17 pounds. Powerful and well balanced, the Shorthair is broad of chest and short of leg with rounded paws and a tail that narrows to a rounded tip from a thick base. Small ears top a sizeable head made to appear even wider thanks to the cat’s full cheeks.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/12/4b9b708f-e321-4e38-ae71-e8d66cd33dc2.jpg\" alt=\"\" title=\"\" width=\"447\" height=\"254\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">Colors</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair comes in many patterns and colors. The blue variant has been widely popular, but in recent years the black, white, red, cream, chocolate, lilac, and cinnamon variations have grown in prevalence. The cats can be all one color, or they can be colorpoints, tabbies, and bicolors.</span></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">GroomingRequirements</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The coat of the British Shorthair can be compared to a carpet with a deep pile. They have more hairs per square inch in their coats than any other breed. Surprisingly, this luxurious coat requires very little care beyond brushing or combing with a wire toothed comb to remove dead and loose hairs. This should be done on a weekly basis to minimize shedding.Photo credit: Eric Isselee/ShutterstockPhoto credit: Eric Isselee/Shutterstock</span></p>","contentType":"HTML","createTime":"2023-10-10T11:18:32","updateTime":null,"wikiType":"dog","wikiState":"IN_REVIEW","creatorId":"1001","collectionCount":0},{"id":"1711581955428470784","title":"拉布拉多犬","cover":"http://s1osmcysm.hn-bkt.clouddn.com/wiki-cover/6/11/3b76baf0-460f-40f5-afa5-d6117aa9551b.jpg","description":" The Ewokian is a designer dog breed developed by crossing two toy dogs, the Havanese and the Pomeranian. Their teddy-bear look charms everybody!  ","about":"[{\"name\":\"Height\",\"value\":\"12-14 inches\"},{\"name\":\"Weight\",\"value\":\"7-17 lb\"},{\"name\":\"Physique\",\"value\":\"Sturdy, big bodied cats\"},{\"name\":\"Lifespan\",\"value\":\"8-10 years\"},{\"name\":\"Best Suited For\",\"value\":\"Families and singles with or without children and pets\"},{\"name\":\"Temperament\",\"value\":\"Calm, easy going, adaptable, affectionate\"},{\"name\":\"Comparable Breeds\",\"value\":\"British Longhair, Ragamuffin\"}]","content":"<h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedHistory</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The oldest of all British cat breeds, the Shorthair traces its lineage back to Roman domestic cats. The ancestors of the British Shorthair were initially prized for their skill as hunters. Big and strong, they were more than a match for any rat or mouse, and thus earned their keep living in proximity to humans. Once a street cat, the British Shorthair is now highly prized as a companion and beloved for its beauty and engaging, loyal personality. The British Shorthair became an officially recognized breed in 1889, principally due to the work of enthusiast Harrison Weir. The first British Shorthair to earn a Grand Champion title under the rules of the Governing Council of the Cat Fancy was a blue male named Brynbuboo Little Monarch. Today, virtually all British Shorthairs trace their lineage back to this one cat.The British Shorthair is an adaptable and calm breed.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/8/a992dcd6-185a-4a29-ae69-0561ed00abd2.jpg\" alt=\"\" title=\"\" width=\"459\" height=\"263\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedTraits</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair is an adaptable and calm breed. They learn quickly, and get along well with all members of the household, although they do tend to bond more deeply with the human of their choice. They don’t like to be carried, but other than that the British Shorthair is an easy going cat that doesn’t have much to say. Somewhat rowdy as kittens, they grow out of their boisterous phase to become excellent companions and members of the family. The British Shorthair is not especially vocal, nor are they bad to wander away. They can be left alone without suffering from either separation anxiety or loneliness.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/11/12/b8f16391-88fb-4bdf-86fd-9b93e34dba30.jpg\" alt=\"\" title=\"\" width=\"446\" height=\"243\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">OverallDescription</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">Often described as having a “teddy bear” appearance, the British Shorthair is a sturdy cat with a large head and big, round, inquisitive eyes. The breed is slow to mature, not reaching full size until at least three years of age. They are big cats, with males reaching as much as 17 pounds. Powerful and well balanced, the Shorthair is broad of chest and short of leg with rounded paws and a tail that narrows to a rounded tip from a thick base. Small ears top a sizeable head made to appear even wider thanks to the cat’s full cheeks.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/12/4b9b708f-e321-4e38-ae71-e8d66cd33dc2.jpg\" alt=\"\" title=\"\" width=\"447\" height=\"254\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">Colors</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair comes in many patterns and colors. The blue variant has been widely popular, but in recent years the black, white, red, cream, chocolate, lilac, and cinnamon variations have grown in prevalence. The cats can be all one color, or they can be colorpoints, tabbies, and bicolors.</span></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">GroomingRequirements</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The coat of the British Shorthair can be compared to a carpet with a deep pile. They have more hairs per square inch in their coats than any other breed. Surprisingly, this luxurious coat requires very little care beyond brushing or combing with a wire toothed comb to remove dead and loose hairs. This should be done on a weekly basis to minimize shedding.Photo credit: Eric Isselee/ShutterstockPhoto credit: Eric Isselee/Shutterstock</span></p>","contentType":"HTML","createTime":"2023-10-10T11:18:32","updateTime":null,"wikiType":"dog","wikiState":"APPROVED","creatorId":"1001","collectionCount":0},{"id":"1711581873048145920","title":"拉布拉多犬","cover":"http://s1osmcysm.hn-bkt.clouddn.com/wiki-cover/15/1/d3f17287-38b6-4fe5-8a30-f9c3c054afa5.jpg","description":" The Ewokian is a designer dog breed developed by crossing two toy dogs, the Havanese and the Pomeranian. Their teddy-bear look charms everybody!  ","about":"[{\"name\":\"Height\",\"value\":\"12-14 inches\"},{\"name\":\"Weight\",\"value\":\"7-17 lb\"},{\"name\":\"Physique\",\"value\":\"Sturdy, big bodied cats\"},{\"name\":\"Lifespan\",\"value\":\"8-10 years\"},{\"name\":\"Best Suited For\",\"value\":\"Families and singles with or without children and pets\"},{\"name\":\"Temperament\",\"value\":\"Calm, easy going, adaptable, affectionate\"},{\"name\":\"Comparable Breeds\",\"value\":\"British Longhair, Ragamuffin\"}]","content":"<h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedHistory</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The oldest of all British cat breeds, the Shorthair traces its lineage back to Roman domestic cats. The ancestors of the British Shorthair were initially prized for their skill as hunters. Big and strong, they were more than a match for any rat or mouse, and thus earned their keep living in proximity to humans. Once a street cat, the British Shorthair is now highly prized as a companion and beloved for its beauty and engaging, loyal personality. The British Shorthair became an officially recognized breed in 1889, principally due to the work of enthusiast Harrison Weir. The first British Shorthair to earn a Grand Champion title under the rules of the Governing Council of the Cat Fancy was a blue male named Brynbuboo Little Monarch. Today, virtually all British Shorthairs trace their lineage back to this one cat.The British Shorthair is an adaptable and calm breed.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/8/a992dcd6-185a-4a29-ae69-0561ed00abd2.jpg\" alt=\"\" title=\"\" width=\"459\" height=\"263\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">BreedTraits</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair is an adaptable and calm breed. They learn quickly, and get along well with all members of the household, although they do tend to bond more deeply with the human of their choice. They don’t like to be carried, but other than that the British Shorthair is an easy going cat that doesn’t have much to say. Somewhat rowdy as kittens, they grow out of their boisterous phase to become excellent companions and members of the family. The British Shorthair is not especially vocal, nor are they bad to wander away. They can be left alone without suffering from either separation anxiety or loneliness.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/11/12/b8f16391-88fb-4bdf-86fd-9b93e34dba30.jpg\" alt=\"\" title=\"\" width=\"446\" height=\"243\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">OverallDescription</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">Often described as having a “teddy bear” appearance, the British Shorthair is a sturdy cat with a large head and big, round, inquisitive eyes. The breed is slow to mature, not reaching full size until at least three years of age. They are big cats, with males reaching as much as 17 pounds. Powerful and well balanced, the Shorthair is broad of chest and short of leg with rounded paws and a tail that narrows to a rounded tip from a thick base. Small ears top a sizeable head made to appear even wider thanks to the cat’s full cheeks.</span></p><p><img src=\"http://s1osmcysm.hn-bkt.clouddn.com/wiki-content-image/10/12/4b9b708f-e321-4e38-ae71-e8d66cd33dc2.jpg\" alt=\"\" title=\"\" width=\"447\" height=\"254\" data-display=\"inline\"></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">Colors</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The British Shorthair comes in many patterns and colors. The blue variant has been widely popular, but in recent years the black, white, red, cream, chocolate, lilac, and cinnamon variations have grown in prevalence. The cats can be all one color, or they can be colorpoints, tabbies, and bicolors.</span></p><h2><strong><span style=\"color: rgb(0, 0, 0);\">GroomingRequirements</span></strong></h2><p><span style=\"color: rgb(124, 124, 124);\">The coat of the British Shorthair can be compared to a carpet with a deep pile. They have more hairs per square inch in their coats than any other breed. Surprisingly, this luxurious coat requires very little care beyond brushing or combing with a wire toothed comb to remove dead and loose hairs. This should be done on a weekly basis to minimize shedding.Photo credit: Eric Isselee/ShutterstockPhoto credit: Eric Isselee/Shutterstock</span></p>","contentType":"HTML","createTime":"2023-10-10T11:18:13","updateTime":null,"wikiType":"dog","wikiState":"IN_REVIEW",