  watch: {},
  computed: {},
  methods: {
    editWiki(item) {
        // 获取wikiID 路由跳转到编辑页面
        let wikiId = item.id;
        this.$router.push({path: '/petswiki/wikiEdit/' + wikiId});
    },
    delWiki(item) {
        // 弹出框
        this.$confirm('确定删除百科?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
        }).then(() => {
        let params = {
            wikiId: item.id,
        }
        apiWiki.deleteWiki(params).then((res)=>{
            if (res.success) {
            // this.wiki = res.result;
                this.$message.success(res.result);
                this.getWikiCardsPage();
            } else {
            this.$message.error(res.message);
            }
        })
        })

    },
    
    handleSizeChange(val) {
        storage.setItem('wikiCardCurrentPage', this.currentPage);
        storage.setItem('wikiCardPageSize', val);
        this.pageSize = val;
        this.getWikiCardsPage()
    },
    handleCurrentChange(val) {
        storage.setItem('wikiCardCurrentPage', val);
        storage.setItem('wikiCardPageSize', this.pageSize);
        this.currentPage = val;
        this.getWikiCardsPage()
    },
    getWikiCardsPage() {
        // 发送请求获取数据
        let params = {
            wikiState: this.wikiState,
            wikiType: this.wikiType,
            currentPage: this.currentPage,
            wikiOrder: this.wikiOrder,
            wikiOrderType: this.wikiOrderType,
            pageSize: this.pageSize,
        }
        apiWiki.getMyWikiCards(params).then(res=>{
            if(res.success){
                this.$message.success('百科Card获取成功');
                this.cardsData = res.result.records;
                this.total = res.result.total;

            }else{
                this.$message.error(res.message);
            }
        })
    },
    myWikiSearch() {
        // 发送请求获取数据
        let params = {
            wikiState: this.wikiState,
            wikiType: this.wikiType,
            currentPage: this.currentPage,
            wikiOrder: this.wikiOrder,
            wikiOrderType: this.wikiOrderType,
            pageSize: this.pageSize,
            searchTag: this.wikiSearchTag,
        }
        apiWiki.getMyWikiCards(params).then(res=>{
            if(res.success){
                this.$message.success('百科Card获取成功');
                this.cardsData = res.result.records;
                this.total = res.result.total;

            }else{
                this.$message.error(res.message);
            }
        })
    },
    toAnimlDetail(id) {
        // 路由跳转到动物详细页面
        this.$router.push({path: '/petswiki/wikiInfo/' + id});
    },
  },
  created() {},
  mounted() {
    console.log(storage.getItem('wikiCardCurrentPage'));
    console.log(storage.getItem('wikiCardPageSize'));
    this.currentPage = storage.getItem('wikiCardCurrentPage')==undefined?1:parseInt(storage.getItem('wikiCardCurrentPage'));
    this.pageSize = storage.getItem('wikiCardPageSize')==undefined?12:parseInt(storage.getItem('wikiCardPageSize'));
    let params = {
        wikiType: this.wikiType,
        currentPage: this.currentPage,
        pageSize: this.pageSize,
    }
    this.getWikiCardsPage()

    // 获取类型选项
    apiWiki.getWikiTemplateOptions().then((res)=>{
        if(res.success){
            this.wikiTypeOptions = JSON.parse(res.result);
        }else{
            this.$message.error(res.message);
        }
    })
  }
};
